<!-- 人员监控 -->
<template>
  <van-row class="dt-home">
    <!-- 顶部蓝色框 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">人员监控</p>
      </van-col>
    </van-row>
    <!--主体-->
    <van-row class="dt-content">
      <van-row class="dt-table">
        <van-row class="dt-item-title">
          今日到岗
        </van-row>
        <!--  -->
        <van-row type="flex"
                 justify="space-around">
          <van-col :sapn="12"
                   class="dt-bg1">
            <div class="dt-word">保安实到人数</div>
            <div class="dt-num">{{baoanNum}}</div>
          </van-col>
          <van-col class="dt-bg2"
                   :sapn="12">
            <div class="dt-word">保洁实到人数</div>
            <div class="dt-num">{{baojieNum}}</div>
          </van-col>
        </van-row>
        <!--  -->
        <van-row class="dt-item-title">
          到岗人员列表
        </van-row>

        <van-row class="dt-list">
          <van-tabs v-model="activeName"
                    color="#5870F8"
                    title-active-color="#5870F8">
            <van-tab>
              <template #title>
                <van-icon class="dt-baoan"
                          :name="require('../../../assets/img/anbao.png')" />保安部
              </template>
              <van-list v-model="loading"
                        :finished="finished"
                        :error.sync="listError"
                        error-text="请求失败，点击重新加载"
                        @load="onLoadList">
                <van-row class="dt-item-list"
                         v-for="item in baoanList"
                         :key="item.id"
                         @click="touchUser(item)">
                  <peopleCard :headPortrait="item.headPortrait"
                              :name="item.name"
                              :position="item.position"></peopleCard>
                </van-row>
              </van-list>
            </van-tab>
            <van-tab>
              <template #title>
                <van-icon class="dt-baojie"
                          :name="require('../../../assets/img/baojie@3x.png')" />保洁部
              </template>
              <van-list v-model="loading"
                        :finished="finished"
                        :error.sync="listError"
                        error-text="请求失败，点击重新加载"
                        @load="onLoadList">
                <van-row class="dt-item-list"
                         v-for="item in baojieList"
                         :key="item.id"
                         @click="touchUser(item)">
                  <peopleCard :headPortrait="item.headPortrait"
                              :name="item.name"
                              :position="item.position"></peopleCard>
                </van-row>
              </van-list>
            </van-tab>
          </van-tabs>
        </van-row>
      </van-row>
    </van-row>
    <!-- 用户详情信息弹出层 -->
    <van-popup v-model="showPop"
               class="dt-pop"
               position="top"
               @close="closePop">
      <userInformation style="heigth: 80%"
                       :information="information"
                       :department="department">
      </userInformation>
      <div class="dt-tongxun"
           @click="seedMessage">
        <div class="imgIco">
          <img :src="require('@/assets/img/通讯录消息.png')">
        </div>
        <div class="imgText">发消息</div>
      </div>
      <div class="dt-tongxun"
           @click="audioCall">
        <div class="imgIco">
          <img :src="require('@/assets/img/通讯录语音.png')">
        </div>
        <div class="imgText">语音通话</div>
      </div>
      <div class="dt-tongxun"
           @click="videoCall">
        <div class="imgIco">
          <img :src="require('@/assets/img/通讯录视频.png')">
        </div>
        <div class="imgText">视频通话</div>
      </div>
    </van-popup>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  CheckDataByCondition
} from "@/api/operationalTesting";
import peopleCard from "@/components/peopleCard/peopleCard"
import {
  detailByUid,
  getDepartmentList
} from "@/api/userApi";
import headPortrait from "@/components/headPortrait/headPortrait";
import userInformation from "@/components/userInformation";
// 提示框
import { Toast } from 'vant';
// 发起会话
import {
  createSingleConversation,
  getIdByName
} from "@/api/message";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    peopleCard,
    headPortrait,
    userInformation,
  },
  data () {
    //这里存放数据
    return {
      baoanNum: 0,
      baojieNum: 0,
      onChange (event) {
        this.setData({
          checked: event.detail,
        });
      },
      // activeName: 'a',
      //
      list: [],
      loading: false,
      finished: false,
      baoanList: [],
      baojieList: [],
      loading: false, // 列表加载状态
      finished: true, // 列表加载完成
      result: [],
      // 弹出层是否显示
      showPop: false,

      // 个人信息
      information: {},
      // 部门
      department: '',
      // 弹出层展开用户的手机号
      popTel: '',
      userName: ''
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getData()
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    //
    getData () {
      let date = new Date()
      let w = date.format('yyyy-MM-ddT00:00:00')
      let q = {
        "CheckDate": w
      }
      console.log("请求参数", q);
      CheckDataByCondition(q).then(response => {
        console.log("请求返回", response);
        let data = response.Data
        console.log("******", data);
        for (let index in data) {
          if (data[index].CheckUserDuty == '保安部') {
            this.baoanNum += 1
            let info =
            {
              ID: index,
              name: data[index].CheckUserName,
              position: '职位',
              uid: data[index].CheckUserId,
            }
            this.baoanList.push(info)
          } else if (data[index].CheckUserDuty == '保洁部') {
            this.baojieNum += 1
            let info =
            {
              ID: index,
              name: data[index].CheckUserName,
              position: '职位',
              uid: data[index].CheckUserId,
            }
            this.baojieList.push(info)
          }
        }
      })
    },

    // 人员点击事件
    touchUser (data) {
      this.userName = data.name
      detailByUid({ uid: data.uid }).then(response => {
        this.showPop = true
        this.information = response.data
        let info = {
          uid: JSON.parse(localStorage.getItem("userInfo")).uid,
          memName: data.real_name
        }
        getIdByName(info).then(res => {
          this.popTel = res.data[0].memTel
        })
      })
      getDepartmentList({ uid: data.uid }).then(response => {
        let depList = response.data.list
        if (depList.length) {
          this.department = depList[depList.length - 1].name
        } else {
          this.department = ''
        }
      })
    },
    // 弹出层关闭事件
    closePop () {
      this.information = {}
      this.department = ''
      this.popTel = ''
      this.userName = ''
    },

    // 语言通话
    audioCall () {
      this.$dialog.confirm({
        message: '是否发起语音通话',
      }).then(() => {
        if (window.android) {
          window.android.voiceChat(this.popTel);
        } else if (window.webkit) {
          window.webkit.messageHandlers.makeAudioCall.postMessage(this.popTel);
        }
      }).catch()
    },
    // 视频通话
    videoCall () {
      this.$dialog.confirm({
        message: '是否发起视频通话',
      }).then(() => {
        if (window.android) {
          window.android.videoChat(this.popTel)
        } else if (window.webkit) {
          window.webkit.messageHandlers.makeVideoCall.postMessage(this.popTel)
        }
      }).catch()
    },

    // 发消息
    seedMessage () {
      // 根据真实姓名或用户名获取 发消息id
      let info = {
        uid: JSON.parse(localStorage.getItem("userInfo")).uid,
        keyword: this.userName
      }
      getIdByName(info).then(res => {
        console.log("resres", res.data[0].id);
        let userId = res.data[0].id
        let query = {
          uid: JSON.parse(localStorage.getItem("userInfo")).uid,
          objectId: userId,
        };
        createSingleConversation(query).then(res => {
          console.log("resres", res);
          if (res.code === 0) {
            Toast('会话创建成功');
            // 跳到会话列表
            let messageId = res.data.id
            this.$router.push({ name: 'MessageChat', query: { id: messageId } })
          } else if (res.code === 1301) {
            Toast('不能给自己发起会话');
          } else if (res.code === 1201) {
            let messageId = res.data.id
            this.$router.push({ name: 'MessageChat', query: { id: messageId } })
          } else {
            Toast('会话创建失败，请稍后再试');
          }
        })

      })


    },

    // 返回首页按钮
    Home () {
      this.$router.push({ name: 'Home' })
    },
    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import "../../../styles/dimens";
.dt-home {
  height: 100%;
  .dt-title {
    width: 100%;
    height: $headerHeight;
    padding-top: $statusBarHeight;
    background-color: #5870f8;
  }
  .dt-back {
    height: px2rem(14);
    width: px2rem(14);
    margin-left: px2rem(6);
    color: #fff;
    font-size: px2rem(18);
  }
  .dt-home-shouye {
    font-size: px2rem(17);
    font-family: "SourceHanSansCN-Medium";
    color: #ecedf2;
  }
  .dt-table {
    float: left;
    width: 94%;
    height: 80vh;
    margin: 0 3%;
  }
  .dt-item-title {
    text-align: left;
    color: #333333;
    font-family: Source Han Sans CN;
    font-size: px2rem(15);
    font-weight: Medium;
    height: px2rem(30);
    width: 100%;
    font-weight: 600;
    padding-top: px2rem(20.5);
  }
  .dt-bg1 {
    width: px2rem(170);
    height: px2rem(81);
    background-image: url("../../../assets/img/卡片1.png");
    background-size: 100%;
  }
  .dt-bg2 {
    width: px2rem(170);
    height: px2rem(81);
    background-image: url("../../../assets/img/卡片2.png");
    background-size: 100%;
  }
  .dt-word {
    font-size: px2rem(18);
    font-weight: Medium;
    color: #ffffff;
    margin-top: px2rem(10.5);
    text-align: left;
    padding-left: px2rem(14);
  }
  .dt-num {
    height: 25px;
    margin-top: 10px;
    line-height: 25px;
    text-align: center;
    font-size: px2rem(16);
    font-weight: Medium;
    color: #ffffff;
    margin-top: px2rem(14.5);
    text-align: left;
    padding-left: px2rem(14);
  }
}
.dt-list {
  width: 100%;
  height: 75%;
  overflow-y: auto;
}
.dt-item-list {
  margin-top: px2rem(24.5);
}
.dt-baoan {
  // border-left: 2px solid #000;
  // margin-left: px2rem(-20);
  padding: px2rem(5.5);
}
.dt-baojie {
  // margin-left: px2rem(-20);
  padding: px2rem(5.5);
}

.dt-pop {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 80%;
}

.imgBox {
  margin: px2rem(20) 0;
  .imgs {
    margin-bottom: px2rem(5);
    width: px2rem(50);
    height: px2rem(50);
  }
}
.dt-tongxun {
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #f0f0f0;
  padding: px2rem(15) 0 px2rem(20);
  .imgIco {
    width: px2rem(22);
    height: px2rem(20);
    margin-right: px2rem(13);
    img {
      width: px2rem(22);
      height: px2rem(20);
    }
  }
  .imgText {
    height: px2rem(20);
    line-height: px2rem(20);
    color: #5b6068;
  }
}

/deep/.van-tabs__wrap {
  margin-left: px2rem(-20);
}
/deep/.van-tab {
  border-left: px2rem(3.9) solid#DADADA;
  // border-height: px2rem(14);
}
/deep/.dt-table[data-v-749979b4] {
  float: left;
  width: 94%;
  margin: 0 3%;
  height: auto;
}
</style>