import myrequest from '@/utils/axios'

import { msapiOnline } from "./onLineBaseUrl";

export function demoGet (pageNum, pageSize, proId) {
  return myrequest({
    url: '/demoGet',
    method: 'get',
    params: { pageNum, pageSize, proId }
  })
}
//考勤
export function CheckDataByCondition (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/YZProjectService/tb_project_checkwork/GetCheckDataByCondition',
    method: 'post',
    data: params
  })
}
